import { mapGetters, mapMutations } from 'vuex';

export default {
  data() {
    return {
      activeTab: null,
      tabs: [
        { name: 'Startseite', tag: 'Home', icon: 'home_w', path: '' },
        { name: 'Über Mich', tag: 'About', icon: 'about_w', path: 'About' },
        {
          name: 'Projekte',
          tag: 'Projects',
          icon: 'projects_w',
          path: 'Projects'
        },
        { name: 'Kenntnisse', tag: 'Skills', icon: 'skills_w', path: 'Skills' },
        { name: 'Kontakt', tag: 'Contact', icon: 'contact_w', path: 'Contact' }
      ]
    };
  },
  mounted() {
    this.tabs.forEach(tab => {
      if (tab.tag.toLowerCase() == this.$router.currentRoute.name) {
        this.navClick(tab);
      }
    });
    this.$router.push('/');
  },
  methods: {
    ...mapMutations(['setActiveTab']),
    navClick(destination) {
      this.tabs.forEach(tab => {
        tab.icon = tab.tag.toLowerCase() + '_w';
      });
      destination.icon = destination.tag.toLowerCase() + '_b';
      this.activeTab = destination;
      this.setActiveTab(this.activeTab);
    },
    getImgUrl(tab) {
      var images = require.context('./assets/', false, /\.png$/);
      return images('./' + tab + '.png');
    }
  },
  computed: {
    ...mapGetters(['getActiveTab'])
  },
  watch: {
    $route() {
      this.tabs.forEach(tab => {
        if (tab.tag.toLowerCase() == this.$router.currentRoute.name) {
          this.navClick(tab);
        }
      });
    }
  }
};
